









import Vue from "vue";
export default Vue.extend({
  data() {
    return {
      text: ""
    };
  },
  props: {
    value: {
      type: String,
      default: ""
    },
    placeholder: {
      type: String
    }
  },
  mounted() {
    this.text = this.value;
  },
  methods: {
    onChange() {
      this.$emit("change", this.text);
    }
  }
});
